




















































































export default {
	data() {
		return {
			PageCur: 'left',
			modalName: null,
			dateFrom: '2019-06-01',
			dateTo: '2019-06-25'
		}
	},
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		navto: function(e) {
			uni.navigateTo({
				url: '/pages/mid/mid'
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		DateChange1(e) {
			this.dateFrom = e.detail.value
		},
		DateChange2(e) {
			this.dateTo = e.detail.value
		}
	}
}
